/* sidebar css start */
.sidebar {
  width: 250px;
  background: #ffffff;
  box-shadow: -8px 12px 18px 0 #b3bcd0;
  position: fixed;
  top: 0;
  left: 0;
  height: 100%;
  z-index: 999;
  @include transition(all 0.3s);
  @media (max-width: 991px) {
    left: -285px; }
  &.open {
    left: 0; }
  &.bg_img {
    background-position: center center;
    background-repeat: no-repeat;
    background-size: cover; }
  .res-sidebar-close-btn {
    position: absolute;
    top: 0;
    right: -35px;
    width: 35px;
    height: 35px;
    background-color: $color--primary;
    color: #ffffff;
    display: none;
    @media (max-width: 991px) {
      display: block; }
    i {
      font-size: 24px; } }
  .sidebar__logo-shape {
    display: none;
    img {
      height: 35px; } }
  &__expand {
    background-color: transparent;
    color: #ffffff;
    position: absolute;
    top: 20px;
    width: 100%;
    text-align: center;
    display: none; }
  &.active {
    width: 70px;
    &::before {
      position: absolute;
      content: '';
      top: 0;
      right: 0;
      width: 9px;
      height: 100%;
      background-color: #f3f5f7; }
    .sidebar__menu {
      .sidebar-menu-item.active {
        &::before {
          opacity: 1;
          visibility: visible; } } }
    .sidebar__menu {
      .sidebar-menu-item.active > a .menu-icon {
        color: #000000 !important; } }
    .sidebar__expand {
      display: block; }
    .sidebar__main-logo {
      display: none; }
    .sidebar__logo-shape {
      display: block; }
    .sidebar__menu-header {
      font-size: 0;
      text-align: center;
      margin: 5px 0;
      &::before {
        font-family: "Font Awesome 5 Free";
        content: "\f141";
        font-size: fsize("16px");
        color: $para-color; } }
    .menu-badge {
      display: none; }
    .sidebar__menu {
      .sidebar-menu-item {
        > a {
          padding: 15px 15px 15px 30px;
          justify-content: center;
          .menu-icon {
            margin-right: 0; }
          .menu-title {
            display: none; } } }
      .sidebar-dropdown {
        > a {
          &::before {
            display: none; } } } }
    .sidebar-submenu {
      &.sidebar-submenu__open {
        display: none!important; }
      .sidebar-menu-item {
        a {
          padding: 10px 20px 10px 35px; } } }
    .navbar__expand {
      display: none; }
