  .slimScrollDiv {
    .slimScrollBar {
      background-color: #000000!important;
      width: 3px!important;
      opacity: .25!important; } }
  &.capsule--rounded {
    .sidebar__menu {
      padding-left: 0;
      > .sidebar-menu-item {
        >a {
          margin-right: 5px;
          @include border-radius(0 999px 999px 0); }
        .sidebar-submenu {
          .sidebar-menu-item {
            margin-right: 0;
            > a {
              margin-right: 15px;
              @include border-radius(0 999px 999px 0); } } } } } }
