    .sidebar__menu {
      > .sidebar-menu-item {
        >a {
          // margin: 0 15px
          @include border-radius(999px); }
        .sidebar-submenu {
          .sidebar-menu-item {
            margin-right: 0;
            > a {
              // margin: 0 15px
              @include border-radius(999px); } } } } } } }
.sidebar__logo {
  padding: 20px 10px;
  @include d-flex;
  align-items: center;
  text-align: center;
  .sidebar__main-logo {
    width: 90%;
    @media (max-width: 991px) {
      width: 100%; } }
  a {
    img {
 } } }      // height: 45px
.navbar__expand {
  margin-left: auto;
  background-color: transparent;
  width: 16px;
  height: 16px;
  // border: 1.5px solid $para-color
  @include border-radius(50%);
  position: relative;
  margin-top: 15px;
  @media (max-width: 991px) {
    display: none; }
  &.active {
    &::before {
      opacity: 0; } }
  &::before {
    position: absolute;
    content: '';
    top: 4px;
    left: 4px;
    width: 7px;
    height: 7px;
    background-color: $para-color;
    @include border-radius(50px);
    opacity: 1;
    @include transition(all 0.3s); }
  &::after {
    position: absolute;
    content: '';
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border: 1.5px solid #ffffff;
    @include border-radius(50%); } }
.sidebar-submenu {
  display: none; }
.sidebar__menu {
  margin-top: 20px;
  // padding: 0 10px
  margin-bottom: 100px;
  .sidebar-menu-item {
    margin-top: 5px;
    > a {
      @include d-flex;
      align-items: center;
      padding: 12px 25px;
      transition: all 0.3s;
      @include border-radius(5px);
      &:hover {
        // background-color: #EBECF0
        padding-left: 25px;
        .menu-icon {
          color: $base-color;
          text-shadow: 1px 2px 5px rgba($base-color, 1); }
        .menu-title {
          color: $base-color; } } }
    .side-menu--open {
 }      // background-color: #EBECF0
    &.active {
      position: relative;
      &::before {
        position: absolute;
        content: '';
        background-image: url('../images/menu-shape.png');
        background-repeat: no-repeat;
        top: -87%;
        right: 0;
        width: 44px;
        height: 125px;
        z-index: -1;
        opacity: 0;
        visibility: hidden; } } }
  .menu-icon {
    color: #5b6e88;
    font-size: fsize("18px");
    margin-right: 15px;
    transition: all 0.5s;
    text-shadow: 1px 2px 5px rgba(#000000, 0.15); }
  .menu-title {
    font-size: fsize("13px");
    color: #5b6e88;
    font-weight: 300;
    letter-spacing: 0.5px; }
  .menu-badge {
    padding: 1px 6px;
    font-size: 0.625rem;
    font-weight: 500;
    border-radius: 3px;
    box-shadow: 0 4px 5px 0 rgba(0, 0, 0, 0.2); }
  .sidebar-dropdown {
    > a {
      padding-right: 40px;
      position: relative;
      &::before {
        position: absolute;
        top: 15px;
        right: 20px;
        font-family: "Font Awesome 5 Free";
        font-weight: 700;
        content: "\f107";
        font-size: 11px;
        color: #5b6e88; } } }
  .sidebar-submenu {
    position: relative;
    &::before {
      position: absolute;
      content: '';
      top: 0;
      left: 27px;
      width: 3px;
      height: 100%;
      border-left: 1px dashed #dcdcdc; }
    &__open {
      display: block; }
    .sidebar-menu-item {
      margin-top: 0;
      &.active {
        a {
          // background-color: $base-color
          .menu-icon {
            color: $base-color; }
          .menu-title {
            color: $base-color; } } }
      a {
        padding: 10px 20px 10px 35px;
        transition: all 0.3s;
        &:hover {
          // padding-left: 40px
          background-color: transparent!important;
          .menu-icon {
            color: $color--primary; }
          .menu-title {
            color: $color--primary; } }
        .menu-icon {
          font-size: 0.75rem; }
        .menu-title {
          font-size: fsize("12px"); } } } } }
.sidebar__menu-header {
  font-size: fsize("11px");
  text-transform: uppercase;
  font-weight: 700;
  color: #b5b5b5;
  margin: 25px 0 8px 25px; }
