.pincode-input-container {
  input {
    &:focus, &:active {
      box-shadow: none;
      border-color: $color--primary; } }
  input ~ input {
    &:focus, &:active {
      border-left-width: 1px; } } }
.custom-switch {
  .custom-control-label {
    &::after {
      top: calc(.25rem + -2px); } } }
.timepicker_wrap {
  .action-next,
  .action-prev {
    position: relative;
    &::before {
      position: absolute;
      content: "\f107";
      font-family: "Font Awesome 5 Free";
      color: #000000;
      left: 23px; } }
  .action-next {
    &::before {
      @include transform(rotate(180deg));
      top: 0; } }
  .action-prev {
    &::before {
      bottom: 0; } } }
.datepicker {
  z-index: 999999; }

.custom-field {
  position: relative;
  &__icon {
    position: absolute;
    width: 40px;
    height: 100%;
    line-height: 43px;
    text-align: center;
    i {
      font-size: 20px; } }
  .form-control {
    padding-left: 40px; } }
