/* body-wrapper css start */
.body-wrapper {
  margin-left: 65px;
  padding: 30px;
  transition: all 0.5s cubic-bezier(0.40, -0.25, 0.25, 1.1);
  overflow-x: hidden;
  @media (max-width: 575px) {
    padding: 30px 10px; }
  &.active {
    margin-left: 250px; }
  @media (max-width: 991px) {
    margin-left: 0 !important; } }
.page-title {
  font-size: fsize("18px");
  display: inline-block; }
.page-breadcrumb {
  padding: 0;
  margin: 0;
  background-color: transparent;
  li {
    font-size: fsize("14px");
    a {
      color: $color--primary; } } }

.activity-list {
  &__item {
    padding-left: 30px;
    position: relative;
    padding-bottom: 30px;
    &::before {
      position: absolute;
      content: '';
      left: 5px;
      top: 4px;
      width: 3px;
      height: 100%;
      border-left: 1px solid #e7d2d2; }
    .dot {
      width: 12px;
      height: 12px;
      display: inline-block;
      position: absolute;
      top: 3px;
      left: 0; }
    .title {
      font-size: fsize("14px");
      margin-bottom: 10px; }
    .date {
      font-weight: 500;
      font-size: fsize("12px"); } }
  .slimScrollBar {
    background-color: #000000 !important;
    width: 1px !important;
    opacity: .25 !important; } }

.data-search-form {
  display: inline-block;
  position: relative;
  .form-control {
    max-width: 350px;
    background-color: #ffffff;
    padding-right: 40px; }
  .data-search-btn {
    background-color: transparent;
    color: $para-color;
    position: absolute;
    top: 0;
    right: 0;
    width: 40px;
    height: 100%;
    border-left: 1px solid #e5e5e5; } }
/* body-wrapper css end */
