/* card css start */
.open-code-btn {
  background-color: #000000;
  color: #ffffff;
  font-size: fsize("12px");
  padding: 5px 10px;
  @include border-radius(5px);
  box-shadow: 0 8px 10px 0 rgba(#000000, 0.2);
  &:hover {
    color: #ffffff; } }

.card {
  border: none;
  box-shadow: 0 0.75rem 1.5rem rgba(18, 38, 63, 0.03);
  @include border-radius(8px);
  overflow: hidden;
  &[class*="border"] {
    border: 1px solid; }
  &.text-white {
    .card-title, .card-text {
      color: #ffffff; } }
  .card-img {
    &.style--horizontal {
      height: 100%;
      object-fit: cover;
      -o-object-fit: cover;
      object-position: center;
      -o-object-position: center; } } }
.card-header {
  background-color: transparent;
  border-bottom: 1px solid rgba(140, 140, 140, 0.125); }
.text-white {
  p {
    color: #ffffff; } }
.code-body {
  margin-top: 30px;
  box-shadow: 0 5px 25px 0 rgba(#000000, 0.1);
  .card-body {
    background-color: #2d2d2d;
    padding: 0; } }
.card-img-overlay {
  z-index: 1;
  &::before {
    position: absolute;
    content: '';
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: #000000;
    opacity: 0.5;
    z-index: -1; } }
.card-img {
  height: 100%;
  object-fit: cover;
  -o-object-fit: cover;
  object-position: center;
  -o-object-position: center; }
.card-img-overlay {
  overflow: hidden; }
.card-footer {
  background-color: #ffffff; }
.alert {
  display: flex;
  align-items: center;
  padding: 0;
  border: none;
  @include border-radius(5px);
  overflow: hidden;
  align-items: stretch;
  button.close {
    position: absolute;
    top: 0;
    right: 0;
    padding: 12px; }
  &__message {
    padding: 12px;
    padding-right: 22px; }
  &__icon {
    padding: 13px 14px;
    background-color: rgba(#000000, 0.1); } }


.influencer-card {
  background-color: #ffffff;
  box-shadow: 0 0.75rem 1.5rem rgba(18, 38, 63, 0.05);
  @include border-radius(5px);
  @include transition(all 0.3s);
  overflow: hidden;
  position: relative;
  &:hover {
    box-shadow: 0 1rem 1.5rem rgba(18, 38, 63, 0.1);
    @include transform(translateY(-5px)); }
  &__header {
    @include d-flex;
    align-items: center;
    justify-content: space-between;
    padding: 15px 20px;
    .left {
      @include d-flex;
      align-items: center;
      margin-right: 10px;
      .thumb + .thumb {
        margin-left: -5px; }
      .thumb {
        width: 30px;
        height: 30px;
        overflow: hidden;
        @include border-radius(50%);
        box-shadow: 0 0 5px 2px rgba(#000000, 0.15);
        img {
          width: 30px;
          height: 30px;
          object-fit: cover;
          -o-object-fit: cover;
          object-position: top;
          -o-object-position: top; } } } }
  &__body {
    .thumb {
      height: 320px;
      @media (max-width: 991px) {
        height: 250px; }
      img {
        width: 100%;
        height: 100%;
        @include object-fit; } } }
  &__footer {
    padding: 30px 20px; }
  &__tags {
    @include d-flex;
    margin: -3px -5px;
    li {
      margin: 3px 5px;
      padding: 1px 10px;
      font-size: 12px;
      background-color: $base-color;
      color: #ffffff;
      @include border-radius(3px);
      &:nth-child(even) {
        background-color: $dark-card--bg; } } }
  .watch-amount {
    font-size: 12px;
    @include border-radius(3px);
    padding: 2px 10px;
    background-color: #f1f1f1;
    color: $base-color;
    i {
      font-size: 16px; }
    &.top--left {
      position: absolute;
      top: 20px;
      left: 20px;
      background-color: #ffffff;
      color: $base-color; }
    &.top--right {
      top: 20px;
      left: auto;
      right: 20px; } }
  .card-details-btn {
    position: absolute;
    bottom: 7px;
    right: -3px;
    @include transform(rotate(-90deg));
    &::before {
      content: '';
      position: absolute;
      z-index: 3;
      bottom: 0;
      left: -6px;
      width: 0;
      height: 0;
      border: 16px solid $base-color;
      z-index: 2; }
    &::after {
      content: '';
      position: absolute;
      z-index: 5;
      width: 0;
      height: 0;
      left: -6px;
      right: 0;
      bottom: 0;
      border: 16px solid;
      border-color: transparent transparent #f3f5f7 #f3f5f7;
      z-index: 2; }
    i {
      position: relative;
      z-index: 3;
      top: -9px;
      right: -3px;
      color: #ffffff;
      @include transform(rotate(90deg)); } }
  &.style--two {
    box-shadow: none;
    @include border-radius(0);
    .influencer-card__body {
      padding: 10px 20px;
      .row {
        margin-left: -5px;
        margin-right: -5px; }
      div[class*="col"] {
        padding-left: 5px;
        padding-right: 5px; }
      .thumb {
        overflow: hidden;
        @include border-radius(5px); } }
    .thumb {
      &.height--md {
        height: 225px; }
      &.height--sm {
        height: 105px; } }
    .influencer-card__footer {
      padding-top: 15px; } }
  &__details {
    padding: 30px;
    background-color: $base-color;
    color: #ffffff !important;
    position: absolute;
    top: 20%;
    left: 0;
    height: 100%;
    z-index: 1;
    opacity: 0;
    visibility: hidden;
    @include transition(all 0.3s);
    &.active {
      opacity: 1;
      visibility: visible;
      top: 0; }
    .top {
      @include d-flex;
      .thumb {
        width: 30px;
        height: 30px;
        overflow: hidden;
        @include border-radius(50%);
        box-shadow: 0 0 5px 2px rgba(#000000, 0.15);
        img {
          width: 30px;
          height: 30px;
          object-fit: cover;
          -o-object-fit: cover;
          object-position: top;
          -o-object-position: top; } } }
    .title {
      color: #ffffff; }
    .influencer-card__details-close {
      position: absolute;
      bottom: 0;
      left: 0;
      &::before {
        content: '';
        position: absolute;
        z-index: 3;
        bottom: 0;
        left: -6px;
        width: 0;
        height: 0;
        border: 16px solid #ffffff;
        z-index: 2; }
      &::after {
        content: '';
        position: absolute;
        z-index: 5;
        width: 0;
        height: 0;
        left: -6px;
        right: 0;
        bottom: 0;
        border: 16px solid;
        border-color: transparent transparent #f3f5f7 #f3f5f7;
        z-index: 2; }
      i {
        position: relative;
        z-index: 3;
        top: -9px;
        right: -3px;
        color: #000000; } } }
  .offer-list {
    @include d-flex;
    margin: -5px;
    li {
      font-size: 12px;
      padding: 2px 10px;
      background-color: rgba(#ffffff, 0.15);
      margin: 5px;
      color: rgba(#ffffff, 0.6);
      @include border-radius(3px);
      span {
        font-weight: 500;
        color: #ffffff; } } } }

.publisher-card {
  background-color: $base-color;
  color: #ffffff !important;
  @include border-radius(5px);
  @include transition(all 0.3s);
  &:hover {
    @include transform(translateY(-5px));
    box-shadow: 0 5px 15px rgba(#000000, 0.25); }
  .action-btn-group {
    &.style--two {
      margin-bottom: -20px; }
    a {
      color: #ffffff;
      font-size: 20px; } }
  &__top {
    padding: 20px;
    .thumb {
      width: 85px;
      height: 85px;
      margin: 0 auto;
      overflow: hidden;
      @include border-radius(50%);
      img {
        width: inherit;
        height: inherit;
        @include object-fit; } }
    .name {
      color: #ffffff; } }
  &__footer {
    padding: 20px;
    border-top: 1px solid #3c3a3a38; }
  .caption-list {
    @include d-flex;
    li {
      width: 50%;
      text-align: center;
      position: relative;
      &:last-child {
        &::after {
          display: none; } }
      &::after {
        position: absolute;
        content: '';
        top: 50%;
        right: 0;
        height: 30px;
        width: 1px;
        margin-top: -15px;
        background-color: #3c3a3a38; } }
    .caption, .value {
      display: block; }
    .caption {
      font-weight: 500; }
    .value {
      font-size: 12px;
      margin-top: 3px; } } }


.client-wrapper {
  .single-client-row + .single-client-row {
    margin-top: 20px; } }

.single-client-row {
  background-color: #ffffff;
  padding: 15px 30px;
  box-shadow: 0 0 10px rgba(#000000, 0.05);
  @include border-radius(5px);
  @include d-flex;
  align-items: center;
  @media (max-width: 767px) {
    justify-content: space-between; }
  .collapse-btn {
    width: 20px;
    font-size: 16px;
    a {
      color: $para-color; } }
  .company-name {
    width: 70%;
    padding-left: 20px;
    @media (max-width: 1199px) {
      width: 60%; }
    @media (max-width: 767px) {
      width: 60%; } }
  .status {
    width: calc(30% - (20px + 66px));
    text-align: left;
    padding-left: 12px;
    @media (max-width: 1199px) {
      width: calc(40% - (20px + 66px)); }
    @media (max-width: 767px) {
      width: auto; } }
  .action {
    width: 66px; }
  .name {
    font-size: 16px; } }
.client-brand, .client-product {
  width: 100%;
  .single-client-row {
    border-top: 1px solid #e5e5e5;
    box-shadow: none;
    @include border-radius(0);
    margin-top: 15px;
    padding: 15px 0;
    margin-left: 40px; } }
.client-brand {
  .name {
    font-size: 15px; }
  .status {
    padding-left: 0; }
  .single-client-row + .single-client-row {
    margin-top: 0; } }
.client-product {
  .single-client-row {
    margin-top: 15px;
    .action {
      width: 86px;
      text-align: right; }
    &:last-child {
      padding-bottom: 0; } }
  .single-client-row + .single-client-row {
    margin-top: 0; }
  .status {
    padding-left: 8px; } }
/* card css end */
