/* table css start */
.table {
  margin-bottom: 0; }
.table {
  th {
    font-size: fsize("12px");
    text-align: center;
    padding: 15px 25px;
 }    // white-space: nowrap
  td {
    font-size: fsize("14px");
    color: $para-color;
    text-align: center;
    padding: 20px 25px;
    vertical-align: middle;
    // white-space: nowrap
    @media (max-width: 575px) {
      white-space: initial; }
    span, p, li {
      font-size: fsize("14px"); } }
  &.style--two {
    border-spacing: 0 15px;
    border-collapse: separate;
    border-radius(8px) {}
    &.table.dataTable {
      margin-top: 0 !important; }
    thead {
      background-color: $base-color;
      tr {
        th {
          border-top: none;
          border-bottom: none;
          color: #ffffff;
          text-align: left;
          padding: 10px 20px;
          // &:first-child
          //   text-align: left
          &:last-child {
            text-align: right; } } } }
    tr {
      th, td {
        font-size: 14px; } }
    tbody {
      tr {
        background-color: #ffffff;
        box-shadow: 0 0 5px rgba(#000000, 0.09);
        @include transition(all 0.3s);
        &:hover {
          box-shadow: 0 0 25px rgba(#000000, 0.12);
          @include transform(translateY(-5px)); }
        td {
          border-top: none;
          text-align: left;
          color: $para-color;
          padding: 20px 20px;
          vertical-align: middle;
          font-size: 14px;
          // &:first-child
          //   text-align: left
          &:last-child {
            text-align: right; } }
        &:first-child {
          td {
            border-top: none; } } } } }
  &.style--three {
    tbody {
      tr + tr {
        td {
          border-top: 1px solid #e5e5e5; } } } } }
table {
  th {
    &:last-child {
      text-align: right; }
    &:first-child {
      text-align: left; } }
  td {
    font-weight: 500;
    &:last-child {
      text-align: right; }
    &:first-child {
      text-align: left;
      font-weight: 600; } }
  .user {
    display: flex;
    align-items: center;
    .thumb {
      width: 40px;
      height: 40px;
      img {
        width: 40px;
        height: 40px;
        @include border-radius(50%);
        object-fit: cover;
        -o-object-fit: cover;
        object-position: center;
        -o-object-position: center;
        border: 2px solid #ffffff;
        box-shadow: 0 5px 10px 0 rgba(0, 0, 0, 0.2); } }
    .name {
      width: calc(100% - 40px);
      padding-left: 10px; } } }
.table-striped {
  tbody {
    tr {
      &:nth-of-type(odd) {
        background-color: rgba(115, 115, 115, 0.05); } } } }
// data table css
div.dataTables_wrapper {
  div.dataTables_filter {
    display: inline-block;
    float: right;
    @media (max-width: 767px) {
      float: none;
      width: 100%;
      margin-top: 20px; }
    input {
      &:focus {
        box-shadow: none;
        border-color: $color--primary; } }
    label {
      color: $para-color; } }
  .dt-buttons {
    button {
      border-radius: 3px !important;
      margin: 0 10px;
      background-color: transparent;
      border: 1px solid #d8d8d8;
      @include border-radius(5px !important);
      &:hover , &:focus, &:active {
        background-color: $color--primary!important;
        border-color: $color--primary!important;
        box-shadow: 0px 0px 7px 2px rgba(115, 103, 240, 0.46)!important;
        span {
          color: #ffffff; } } } }
  .dataTables_info {
    float: left;
    color: $para-color;
    font-size: fsize("14px");
    @media (max-width: 767px) {
      float: none; } }
  .dataTables_paginate {
    float: right;
    @media (max-width: 767px) {
      float: none;
      margin-top: 15px!important; } }
  .dataTables_length {
    label {
      color: $para-color;
      select {
        margin-left: 10px;
        margin-right: 10px; } } } }
table.dataTable {
  margin-top: 20px !important;
  margin-bottom: 20px !important;
  tbody {
    tr {
      &:first-child {
        td {
          border-top: 1px solid #e5e5e5; } }
      &:last-child {
        td {
          border-bottom: 1px solid #e5e5e5; } }
      td {
        border: none;
        white-space: nowrap; } } }
  thead {
    tr {
      background-color: $color--primary;
      th {
        border: none;
        color: #ffffff;
        white-space: nowrap; } } } }
.dataTables_paginate {
  .pagination {
    flex-wrap: wrap;
    margin: -5px -7px;
    .page-item {
      margin: 5px 7px;
      &.active {
        .page-link {
          background-color: $color--primary;
          border-color: $color--primary;
          box-shadow: 0px 0px 7px 2px rgba(115, 103, 240, 0.46); } }
      .page-link {
        font-size: fsize("14px");
        color: $para-color;
        @include border-radius(5px !important);
        width: auto;
        height: auto;
        padding: 8px 15px;
        &:focus {
          box-shadow: 0px 0px 7px 2px rgba(115, 103, 240, 0.46); } } } } }

table.table--light {
  thead {
    th {
      border: none;
      color: #ffffff;
      background-color: $base-color; } } }
.customer-details {
  @include d-flex;
  align-items: center;
  .thumb {
    width: 45px;
    height: 45px;
    img {
      width: 45px;
      height: 45px;
      object-fit: cover;
      -o-object-fit: cover;
      object-position: center;
      -o-object-position: center;
      @include border-radius(50%); } }
  .content {
    padding-left: 15px; } }
.user-table-list {
  display: -ms-flexbox;
  display: flex;
  justify-content: center;
  .user + .user {
    margin-left: -10px;
    z-index: 1; }
  .user {
    width: 32px;
    height: 32px;
    @include transition(all 0.3s);
    position: relative;
    z-index: 2;
    &:hover {
      z-index: 2;
      @include transform(scale(1.05, 1.05)); }
    img {
      width: 32px;
      height: 32px;
      @include border-radius(50%);
      object-fit: cover;
      -o-object-fit: cover;
      object-position: center;
      -o-object-position: center;
      border: 2px solid #ffffff;
      box-shadow: 0 5px 10px 0 rgba(#000000, 0.15); } } }

// table responsive
[data-label] {
  position: relative;
  &::before {
    position: absolute;
    content: attr(data-label);
    font-weight: 700;
    color: #000000;
    top: 0;
    left: 0;
    padding: 13px 15px;
    display: none;
    font-size: 12px; } }
.table-responsive--lg {
  @media (max-width: 1199px) {
    thead {
      display: none; }
    tbody {
      tr {
        &:nth-child(odd) {
          background-color: #7367f021;
          td {
            border-top: 1px solid #ffffff !important; } }
        &:nth-child(even) {
          td {
            border-top: 1px solid #e5e5e5; } } } }
    tr {
      th, td {
        display: block;
        padding-left: 45%!important;
        text-align: right!important; } }
    .user {
      justify-content: flex-end;
      .name {
        width: auto; } }
    .user-table-list, .customer-details {
      justify-content: flex-end; }
    [data-label] {
      &::before {
        display: block; } } } }
