*[class*="table-responsive--"] {
  &.data-label--none {
    @media (max-width: 1199px) {
      tr {
        th, td {
          padding-left: .75rem; } } } } }
*[class*="table-responsive--"] {
  .table-dark {
    tbody {
      [data-label] {
        &::before {
          color: $para-color; } } } } }
.table-responsive--lg {
  .table-dark {
    @media (max-width: 1199px) {
      tbody {
        tr {
          &:nth-child(odd) {
            background-color: #343a40; }
          &:nth-child(even) {
            background-color: #222930; } } } } } }
.table-responsive--md {
  .table-dark {
    @media (max-width: 991px) {
      tbody {
        tr {
          &:nth-child(odd) {
            background-color: #343a40; }
          &:nth-child(even) {
            background-color: #222930; } } } } } }
