.pagination {
  .page-item {
    &.active {
      .page-link {
        background-color: $color--primary;
        border-color: $color--primary;
        color: #ffffff;
        box-shadow: 0 4px 10px 0 rgba(0,0,0,0.25); } }
    &.previous, &.next {
      .page-link {
        font-size: 0;
        position: relative;
        width: 35px;
        height: 35px; } }
    &.previous {
      .page-link {
        &::before {
          position: absolute;
          top: 7px;
          right: 11px;
          font-family: "Font Awesome 5 Free";
          font-weight: 700;
          content: "\f104";
          font-size: 16px; } } }
    &.next {
      .page-link {
        &::before {
          position: absolute;
          top: 7px;
          right: 11px;
          font-family: "Font Awesome 5 Free";
          font-weight: 700;
          content: "\f105";
          font-size: 16px; } } }
    &.next {}
    .page-link, span {
      font-size: .875rem;
      display: flex;
      width: 36px;
      height: 36px;
      margin: 0 3px;
      padding: 0;
      border-radius: 50% !important;
      align-items: center;
      justify-content: center;
      color: $para-color; } } }
/* table css end */
