/* registration css start */
.registration-section {
  padding: 120px 100px;
  @include d-flex;
  min-height: 100vh;
  align-items: center;
  background-color: #ffffff;
  position: relative;
  overflow: hidden;
  @media (max-width: 575px) {
    padding: 80px 30px; }
  @media (max-width: 480px) {
    padding: 80px 0; }
  .shape {
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    @media (max-width: 991px) {
      display: none; }
    img {
      height: 100%;
      object-fit: cover;
      -o-object-fit: cover;
      object-position: top;
      -o-object-position: top; } } }
.registration-wrapper {
  width: 100%; }
.registration-content {
  @media (max-width: 991px) {
    padding: 50px;
    background-color: $base-color;
    text-align: center;
    @include border-radius(10px);
    box-shadow: 0 5px 15px rgba(#000000, 0.15); }
  @media (max-width: 380px) {
    padding: 50px 30px; } }
.registration-form {
  margin-right: 100px;
  @media (max-width: 1450px) {
    margin-right: 50px; }
  @media (max-width: 1199px) {
    margin-right: 0; } }
/* registration css end */


/* login css start */
.login-section {
  padding: 120px 100px;
  @include d-flex;
  min-height: 100vh;
  align-items: center;
  background-color: #ffffff;
  overflow: hidden;
  @media (max-width: 575px) {
    padding: 80px 30px; }
  @media (max-width: 480px) {
    padding: 80px 0; }
  .shape {
    position: absolute;
    top: 0;
    right: 0;
    height: 100%;
    @media (max-width: 1550px) {
      width: 120%; }
    @media (max-width: 1199px) {
      width: 130%; }
    @media (max-width: 991px) {
      display: none; }
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      -o-object-fit: cover;
      object-position: top;
      -o-object-position: top; } } }
.login-wrapper {
  width: 100%; }
.login-content {
  @media (max-width: 991px) {
    padding: 50px;
    background-color: $base-color;
    text-align: center;
    @include border-radius(10px);
    box-shadow: 0 5px 15px rgba(#000000, 0.15); }
  @media (max-width: 380px) {
    padding: 50px 30px; } }
.login-form {
  margin-right: 100px;
  @media (max-width: 1450px) {
    margin-right: 50px; }
  @media (max-width: 1199px) {
    margin-right: 0; } }
/* login css end */


/* login css start */
.form-area {
  background-color: $color--primary;
  min-height: 100vh;
  @include d-flex;
  justify-content: center;
  align-items: center;
  padding: 50px 30px;
  @media ( max-width: 575px) {
    padding: 30px 15px; }
  .form-wrapper {
    width: 500px;
    padding: 50px 30px;
    background-color: #ffffff;
    text-align: center;
    position: relative;
    z-index: 9;
    @include border-radius(10px);
    @media ( max-width: 575px) {
      padding: 30px 25px; }
    &::before {
      position: absolute;
      content: '';
      bottom: -25px;
      left: -25px;
      background-color: $color--primary;
      width: 100%;
      height: 100%;
      z-index: -10;
      @include border-radius(10px);
      @media (max-width: 575px) {
        display: none; } }
    &::after {
      position: absolute;
      content: '';
      top: 0;
      right: 0;
      background-color: #ffffff;
      width: 100%;
      height: 100%;
      z-index: -10;
      box-shadow: 0 25px 90px 0 rgba(#000000, 0.6);
      @include border-radius(10px); } } }
.cmn-form {
  text-align: left;
  .form-group {
    position: relative;
    label {
      color: $para-color;
      font-weight: 500;
      font-size: fsize("13px"); }
    .input-icon {
      position: absolute;
      top: 33px;
      right: 4px;
      width: 32px;
      height: 32px;
      background-color: #e3ebf1;
      color: $para-color;
      justify-content: center;
      align-items: center;
      @include d-flex;
      @include border-radius(50%);
      font-size: 20px; } }
  .form-control {
    padding-right: 30px;
    &:focus {
      box-shadow: none; } }
  .submit-btn {
    width: 100%;
    background-color: $color--primary;
    color: #ffffff;
    padding: 10px 45px;
    @include d-flex;
    align-items: center;
    justify-content: center;
    box-shadow: 0 8px 15px 0 rgba(115, 103, 240, 0.5);
    i {
      font-size: 20px;
      margin-left: 5px; } } }
/* login css end */
